
import { mapGetters } from "vuex";
import debugArea from "@/components/debug-area";
export default {
  components: {
    debugArea,
  },
  beforeCreate() {
    this.$store.dispatch("fetchDoctorsAndSpecs");
  },
  data() {
    return {
      doctorToAppointment: undefined,
    };
  },
  methods: {
    onResize(size) {
      this.$nextTick(() => {
        parent.postMessage(
          JSON.stringify({
            action: "resize",
            height: size.height,
          }),
          "*"
        );
      });
    },
  },
  computed: {
    ...mapGetters(["IS_LOADING"]),
  },
};
